// Шрифты

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Open Sans"),
    url("../fonts/open-sans-v26-latin_cyrillic-700.woff2") format("woff2"),
    url("../fonts/open-sans-v26-latin_cyrillic-700.woff") format("woff");
}

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: local("Open Sans"),
    url("../fonts/open-sans-v26-latin_cyrillic-300.woff2") format("woff2"),
    url("../fonts/open-sans-v26-latin_cyrillic-300.woff") format("woff");
}


@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Open Sans"),
    url("../fonts/open-sans-v26-latin_cyrillic-regular.woff2") format("woff2"),
    url("../fonts/open-sans-v26-latin_cyrillic-regular.woff") format("woff");
}
